html,
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  touch-action: none;
}

#map {
  width: 100vw;
  height: 100vh;
  background: #f0f0f0;
  cursor: grab;
  position: relative;
}

svg {
  user-select: none;
  display: block;
  touch-action: none;
}

#inner {
  transform-origin: 0 0;
  transition: none;
}

.map svg path[data-locationtype=location]:hover {
  cursor: pointer;
  opacity: 1;
  transition: 0.3s;
}

.balloon {
  background-color: #fff;
  width: 230px;
  position: absolute;
  display: none;
  background: #fff;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  min-width: 200px;
}

.balloon__close-button {
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 40px;
  cursor: pointer;
}